<template>
    <div class="deal-block">
        <div class="title">{{ title }}</div>
        <transition-group name="cond-list" tag="ul">
            <li class="conditions list-complete-item" v-for="(cond, i) in conditionsArray" :key="i">
                <div class="row-block">
                    <Condition :blockObject="cond" @child_input="changingChild(i, $event)"/>
                    <Variants :isActive="cond.isActive" @selecting="selectingVar(i, $event)"/>
                </div>
                <div class="controls">
                    <div class="control add" @click="addCondition(i)">+</div>
                    <div class="control change" @click="changeCondition(i)">#</div>
                    <div class="control remove" @click="deleteCondition(i)" v-if="(i != 0) || (conditionsArray.length > 1)">
                        <svg class="svg-icon svg-controls--button-unlink-dims js-linked-entity-unlink" title="Удалить">
                            <path id="fgDelete" class="fgcls-1" d="M7923 391a7 7 0 117-7 7 7 0 01-7 7zm0-12.963a5.963 5.963 0 105.96 5.963 5.963 5.963 0 00-5.96-5.963zm2.49 9.182l-2.49-2.486-2.49 2.486-.73-.734 2.49-2.485-2.49-2.485.73-.733 2.49 2.485 2.49-2.485.73.733-2.49 2.485 2.49 2.485z" transform="translate(-7916 -377)"></path>
                        </svg>
                    </div>
                </div>
            </li>
        </transition-group>
    </div>
</template>

<script>

import Condition from './repeated-deal.vue'
import Variants from './variants.vue'

export default {
  name: 'RepeaterCondition',
  data() {
      return {
        //   conditions: this.conditionsArray
      }
  },
  mounted() {
     for (const cond of this.conditionsArray) {
        cond.isActive = false;
    }
  },
  created() {
    document.addEventListener('click', (e) => {
        if (!e.target.closest('.change')) {
            for (const cond of this.conditionsArray) {
                cond.isActive = false;
            }
        }
    });
  },
  props: {
        title: String,
        conditionsArray: Array,
  },
  methods: {
        addCondition(i) {
            this.conditionsArray.splice(i + 1, 0, {
                title: 'Бюджет',
                beforeInput: 'от',
                inputType: 'number',
                placeholder1: '0',
                value1: '',
                betweenInput: 'до',
                placeholder2: '0',
                value2: '',
                afterInput: 'руб.',
                inputs: 'double',
                isActive: false,
                variables: false
            });
            
            this.$emit('change_state', this.conditionsArray);
        },
        deleteCondition(i) {
            this.conditionsArray.splice(i, 1);
            this.$emit('change_state', this.conditionsArray);
        },
        changeCondition(i) {
            this.conditionsArray[i].isActive = !this.conditionsArray[i].isActive;
            this.$emit('change_state', this.conditionsArray);
        },
        selectingVar(i, $event){
            let conditionSettings = this.conditionsArray[i];
            conditionSettings.title = $event.name ? $event.name : '';
            conditionSettings.value1 = $event.value1 ? $event.value1 : '';
            conditionSettings.value2 = $event.value2 ? $event.value2 : '';
            conditionSettings.variables = $event.variables ? $event.variables : false;
            conditionSettings.isActive = false;

            if ($event.type == 'string') {
                conditionSettings.beforeInput = '';
                conditionSettings.betweenInput = '';
                conditionSettings.afterInput = '';
                conditionSettings.placeholder1 = '';
                conditionSettings.inputs = 'single';
                conditionSettings.inputType = 'text';
            } else if ($event.type == 'boolean') {
                conditionSettings.beforeInput = '';
                conditionSettings.betweenInput = '';
                conditionSettings.afterInput = '';
                conditionSettings.inputs = 'single';
                conditionSettings.inputType = 'checkbox';
            } else if ($event.type == 'integer') {
                conditionSettings.beforeInput = 'от';
                conditionSettings.placeholder1 = '0',
                conditionSettings.betweenInput = 'до';
                conditionSettings.placeholder2 = '0',
                conditionSettings.afterInput = '';
                conditionSettings.inputs = 'double';
                conditionSettings.inputType = 'number';
            } else if ($event.type == 'date') {
                conditionSettings.beforeInput = 'с';
                conditionSettings.betweenInput = 'по';
                conditionSettings.afterInput = '';
                conditionSettings.placeholder1 = '';
                conditionSettings.inputs = 'double';
                conditionSettings.inputType = 'date';
            }

            this.$emit('change_state', this.conditionsArray);
        },
        changingChild(i, $event) {
            if ($event.number == 1){
                this.conditionsArray[i].value1 = $event.event.value1;
            } else if ($event.number == 2) {
                this.conditionsArray[i].value2 = $event.event.value2;
            }

            this.$emit('change_state', this.conditionsArray);
        }
  },
  components: {
    Condition, Variants
  }
}

</script>

<style lang="css">
    /* .cond-list-item {
        transition: all .5s;
        display: block;
        margin-right: 10px;
    }
    .cond-list-enter, .cond-list-leave-to {
        opacity: 0;
        transform: translateX(30px);
    }
    .cond-list-leave-active {
        position: absolute;
    }
    .cond-list-move {
        transition: transform 1s;
    } */
    .svg-controls--button-unlink-dims {
        width: 15px;
        height: 15px;
        fill: red;
    }
</style>

<style lang="sass">
    .deal-block
        .title
            margin-bottom: 10px
        ul
            list-style: none
        .conditions
            display: flex
            .controls
                display: flex
                align-items: center
                margin-bottom: 10px
                .control
                    margin-left: 10px
                    border-radius: 50%
                    width: 15px
                    height: 15px
                    display: flex
                    justify-content: center
                    align-items: center
                    color: #fff
                    font-size: 12px
                    font-weight: 700
                    line-height: 1
                    cursor: pointer
                    &:hover
                        opacity: 0.8
                .add
                    background-color: #1976d2
                .change
                    background-color: #c0ca33

</style>