<template>
    <div id="market_color_deals">
        <div id="market-message">
            <span></span>
        </div>
        <div class="widget-container">
            <transition-group name="list-complete" tag="p">
                <div class="repeater-container list-complete-item"
                    v-for="(conditionObject, index) in $store.state.conditions" :key="index">
                    <RepeaterCondition :title="conditionObject.title" :conditionsArray="conditionObject.settings"
                        @change_state="updateStatefromChild(index, $event)"></RepeaterCondition>
                    <div class="controls">
                        <div class="delete" @click='deleteCondition(index)' v-if="conditions.length > 1">Удалить</div>
                        <div class="picker">
                            <ColorPicker :index="index" :bgColor="conditionObject.color" />
                        </div>
                        <div class="checks">
                            <label>
                                <input type="checkbox" v-model="$store.state.conditions[index].isBorder">
                                <div class="label">
                                    <div class="tick"></div>
                                </div>
                                Рамка
                            </label>
                            <label>
                                <input type="checkbox" v-model="$store.state.conditions[index].isBg">
                                <div class="label">
                                    <div class="tick"></div>
                                </div>
                                Фон
                            </label>
                        </div>
                    </div>
                </div>
            </transition-group>
            <div class="flex">
                <div class="market-row">
                    <a href="#" class="market-add-row" @click.prevent='addConditionsGroup'>Добавить группу</a>
                </div>
                <div class="market-row">
                    <button type="submit" class="button-input button-input_blue">
                        <span class="button-input-inner__text" @click='save'>Сохранить</span>
                        <div class="button-input__spinner"><span
                                class="button-input__spinner__icon spinner-icon spinner-icon-white"></span></div>
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import RepeaterCondition from './components/repeated-condition.vue'
    import ColorPicker from './components/color-picker.vue'
    import Vuex from 'vuex';
    const axios = require('axios').default

    Vue.use(Vuex);

    // const domain = window.AMOCRM.widgets.system.subdomain

    function generateRandomColor() {
        let color = "" + Math.floor(Math.random() * 255).toString(16) + Math.floor(Math.random() * 255).toString(16) +
            Math.floor(Math.random() * 255).toString(16);
        while (color.length < 6) {
            color += 'e';
        }
        return '#' + color;
    }

    let StartState;
    if (window.market) {
        if (window.market.ColorDeal) {
            if (window.market.ColorDeal.Conditions) {
                StartState = window.market.ColorDeal.Conditions;
            } else {
                StartState = [{
                    title: 'Для всех сделок, у которых:',
                    color: generateRandomColor(),
                    isBorder: true,
                    isBg: false,
                    settings: [{
                        title: 'Бюджет',
                        beforeInput: 'от',
                        inputType: 'number',
                        placeholder1: '0',
                        value1: '',
                        betweenInput: 'до',
                        placeholder2: '0',
                        value2: '',
                        afterInput: 'руб.',
                        inputs: 'double',
                        isActive: false,
                    }]
                }]
            }
        }
    }

    const store = new Vuex.Store({
        state: {
            conditions: StartState
        },
        mutations: {
            addConditionsGroup(state, dataObject) {
                state.conditions.push(dataObject);
            },
            removeConditionsGroup(state, i) {
                state.conditions.splice(i, 1);
            },
            changeConditionsGroup(state, data) {
                state.conditions[data.index].settings = data.object;
                state.conditions[data.index].settings.isActive = false;
            },
        }
    })

    export default {
        name: 'App',
        store,
        data() {
            return {
                conditions: [{
                    title: 'Для всех сделок, у которых:',
                    color: generateRandomColor(),
                    isBorder: true,
                    isBg: false,
                    settings: [{
                        title: 'Бюджет',
                        beforeInput: 'от',
                        inputType: 'number',
                        placeholder1: '0',
                        value1: '',
                        betweenInput: 'до',
                        placeholder2: '0',
                        value2: '',
                        afterInput: 'руб.',
                        inputs: 'double',
                        isActive: false,
                    }]
                }],
            }
        },
        methods: {
            addConditionsGroup(index) {
                store.commit('addConditionsGroup', {
                    title: 'Для всех сделок, у которых:',
                    color: generateRandomColor(),
                    isBorder: true,
                    isBg: false,
                    settings: [{
                        title: 'Бюджет',
                        beforeInput: 'от',
                        inputType: 'number',
                        placeholder1: '0',
                        value1: '',
                        betweenInput: 'до',
                        placeholder2: '0',
                        value2: '',
                        afterInput: 'руб.',
                        inputs: 'double',
                        isActive: false,
                    }]
                });

            },
            updateStatefromChild(index, $event) {
                store.commit('changeConditionsGroup', {
                    index: index,
                    object: $event
                });
                // store.commit('showState');
                // console.log($event);
            },
            deleteCondition(index) {
                store.commit('removeConditionsGroup', index);
            },
            save() {
                axios({
                    method: "post",
                    url: "http://core.market.ru/color_deals/index.php?request=save",
                    data: self.conditions
                }).then(function (response) {
                    console.log(response);
                    self.conditions = response.data
                })
                .catch(function (error) {
                    console.log(error);
                });
            }
        },
        components: {
            RepeaterCondition,
            ColorPicker
        },
        mounted() {
            const self = this;

            axios({
                method: "get",
                url: "http://core.market.ru/color_deals/index.php?request=get"
            }).then(function (response) {
                console.log(response);
                if (response.data !== false) {
                    self.conditions = response.data
                }
            })
            .catch(function (error) {
                console.log(error);
            });
        },
    }
</script>


