        padding: 20px
        font-family: Tahoma, sans-serif
    .repeater-container
        margin-bottom: 35px
        .deal-block
            width: max-content
        .delete
            background-color: #f44336
            border-radius: 5px
            padding: 5px 10px
            width: 100px
            color: #fff
            text-align: center
            cursor: pointer
            &:hover
                opacity: 0.8
    .buttons
        border-top: 1px solid #eee
        padding-top: 15px
    .controls
        display: flex
        align-items: center
        .delete
            margin-right: 15px
        .checks
            margin-right: 15px
            display: flex
            label
                display: flex
                cursor: pointer
            .label
                display: inline-block
                margin-left: 25px
                margin-right: 10px
                width: 15px
                height: 15px
                border: 2px solid #b2b2b2
                border-radius: 10%
                transition: 400ms 100ms ease-out
                &:hover
                    border-color: #1976d2
                .tick
                    position: relative
                    right: calc(15px * -0.5)
                    top: calc(15px * -0.25)
                    width: calc(15px * 0.25)
                    height: calc(15px * 0.75)
                    border-right: calc(15px * 0.25) solid #f44336
                    border-bottom: calc(15px * 0.25) solid #f44336
                    transform: rotate(45deg) scale(0)
                    opacity: 0
                    transition: all 600ms cubic-bezier(0.175, 0.885, 0.32, 1.5)
                    &:before
                        content: ''
                        position: absolute
                        left: calc(15px * -0.125)
                        bottom: calc(15px * -0.25)
                        border: calc(15px * 0.125) solid #f44336
                        border-radius: 50%
                    &:after
                        content: ''
                        position: absolute
                        right: calc(15px * -0.25)
                        top: calc(15px * -0.125)
                        border: calc(15px * 0.125) solid #f44336
                        border-radius: 50%
            input[type="checkbox"]
                display: none
                &:checked + .label .tick
                    opacity: 1
                    transform: rotate(45deg) scale(1)   
</style>
