<template>
    <div class="select">
        <transition name="fade">
            <div class="wrap" v-if="isActive">
                <div class="option"  v-for="(option, index) in select" :key="index" @click="returnSelected(index)">
                    {{ option.name }}
                </div>
            </div>
        </transition>
    </div>
</template>

<script>

export default {
    name: 'Variants',
    props: {
        isActive: Boolean
    },
    data() {
        return {
            select: [
                {
                    name: 'Бюджет',
                    type: 'integer',
                    variables: false,
                },
                {
                    name: 'Ответственный',
                    type: 'string',
                    variables: window.market ? (window.market.Managers ? window.market.Managers.Names : false) : false,
                },
                {
                    name: 'Название компании',
                    type: 'string',
                    variables: window.market ? (window.market.Companies ? window.market.Companies.Names : false) : false,
                },
                {
                    name: 'Имя контакта',
                    type: 'string',
                    variables: window.market ? (window.market.Contacts ? window.market.Contacts.Names : false) : false,
                },
                {
                    name: 'Дата создания',
                    type: 'date',
                    variables: false
                }
            ]
        }
    },
    methods: {
        returnSelected(i) {
            this.$emit('selecting', {
                name: this.select[i].name,
                type: this.select[i].type,
                variables: this.select[i].variables
            })
        }
    },
    created() {
        if (window.market) {
            if (window.market.ColorDeal) {
                this.select = this.select.concat(window.market.ColorDeal.Variants);
            }
        }
    },
}   
</script>

<style lang="css">
    .fade-enter-active, .fade-leave-active {
        transition: all .2s;
    }
    .fade-enter, .fade-leave-to {
        opacity: 0;
        transform: translateY(40px)
    }
</style>

<style lang="sass">
    .select
        position: relative
        .wrap
            position: absolute
            top: 0
            left: 0
            width: 100%
            border: 1px solid #eee
            max-height: 200px
            background-color: #fff
            border-radius: 5px
            overflow: auto
            z-index: 2
            .option
                padding: 10px 20px
                &:hover
                    cursor: pointer
                    background-color: #eee

</style>