<template>
    <div class="picker-wrap">
        <input 
            type="text" 
            @click="openPicker" 
            @input="changeColor($event)"
            :style="{borderColor: bgColor}"
            class="picker-button"
            :value="bgColor"
        >
        <div ref="picker" class="picker-modal" v-show="isOpened" v-bind:style="{borderColor: bgColor}">
            <div class="picker-close" @click="closePicker">&times;</div>
            <div class="color-picker"></div>
        </div>
    </div>
</template>


<script>
    const ColorPicker = require(`../assets/colorpicker.js`).ColorPicker

    export default {
        name: 'ColorPicker',
        data() {
            return {
                isOpened: false,
            }
        },
        props: {
            bgColor: String,
            index: Number
        },
        methods: {
            openPicker(){
                this.isOpened = true;
            },
            closePicker(){
                this.isOpened = false;
            },
            changeColor($event) {
                window.colorPickerObject[this.index].value = $event.target.value.replace('#', '');
            }
        },
        mounted() {
            const self = this;
            if (!window.colorPickerObject) {
                window.colorPickerObject = [];
            }

            window.colorPickerObject[this.index] = new ColorPicker({
                dom: self.$refs.picker.querySelector('.color-picker'),
                value: self.bgColor
            })
            
            window.colorPickerObject[this.index].addEventListener('change', event => {
                self.$parent.$parent.conditions[this.index].color = '#' + window.colorPickerObject[this.index]._value;
            })
        },
    }
</script>
<style lang="sass">
.picker-wrap
    position: relative
    .picker-button
        width: 100px
        height: 15px
        cursor: pointer
        transition: 0.3s
        border-width: 2px 
        border-style: solid
        border-radius: 3px
        font-size: 15px
        padding: 8px 9px 7px
        color: #313942
        background: #fff
    .picker-modal
        position: absolute
        top: 110%
        left: 0
        width: 200px
        height: 200px
        border-width: 1px 
        border-style: solid
        background: #fff
        z-index: 10
        .picker-close
            text-align: right
            font-size: 30px
            line-height: 1
            cursor: pointer
</style>
<style lang="css">
    .color-picker,
    .color-picker *,
    .color-picker *::before,
    .color-picker *::after {
        border: 0;
        margin: 0;
        padding: 0;
        display: block;
        box-sizing: border-box;
    }

    .color-picker {
        background-color: #fff;
        display: block;
        min-width: 128px;
        min-height: 170px;
        position: relative;
    }

    .color-picker>.color-picker-right-bar {
        border: solid #000000 1px;
        background: linear-gradient(#ff0000,
                #ff00ff,
                #0000ff,
                #00ffff,
                #00ff00,
                #ffff00,
                #ff0000);
        width: 16px;
        height: calc(100% - 46px);
        position: absolute;
        right: 12px;
        top: 12px;
    }

    .color-picker .color-picker-right-bar-picker {
        position: absolute;
        width: 100%;
        height: 1px;
    }

    .color-picker .color-picker-right-bar-picker::before,
    .color-picker .color-picker-right-bar-picker::after {
        content: '';
        width: 10px;
        height: 7px;
        position: absolute;
        background: transparent;
        border: solid transparent 5px;
        border-width: 3.5px 5px;
        top: -3px;
    }

    .color-picker .color-picker-right-bar-picker::before {
        border-left: solid #404040 5px;
        left: -6px;
    }

    .color-picker .color-picker-right-bar-picker::after {
        border-right: solid #404040 5px;
        right: -6px;
    }

    .color-picker>.color-picker-gradients {
        position: absolute;
        width: calc(100% - 50px);
        height: calc(100% - 46px);
        border: solid #000000 1px;
        left: 12px;
        top: 12px;
    }

    .color-picker>.color-picker-gradients,
    .color-picker>.color-picker-gradients * {
        cursor: crosshair;
    }

    .color-picker>.color-picker-gradient-black {
        background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
    }

    .color-picker>.color-picker-color-block {
        position: absolute;
        border: solid #000000 1px;
        background: #fff;
        width: calc(100% - 104px);
        max-width: 72px;
        height: 18px;
        left: 12px;
        bottom: 8px;
    }

    .color-picker>.color-picker-input {
        position: absolute;
        width: 56px;
        border: solid #909090 1px;
        text-align: left;
        height: 18px;
        bottom: 8px;
        right: 12px;
    }

    .color-picker>.color-picker-input>input {
        padding: 0 4px;
        text-align: center;
    }

    .color-picker>.color-picker-input * {
        font-size: 12px;
        color: #808080;
        line-height: 18px;
    }

    .color-picker>.color-picker-input::before {
        width: 16px;
        height: 18px;
        display: block;
        position: absolute;
        border: solid #909090 1px;
        border-right: 0;
        content: '#';
        text-align: center;
        font-size: 12px;
        color: #808080;
        line-height: 18px;
        top: -1px;
        left: -16px;
    }

    .color-picker .color-picker-circle {
        background-color: transparent;
        position: absolute;
    }

    .color-picker .color-picker-circle::before {
        border-radius: 50%;
        width: 11px;
        height: 11px;
        border: solid #000000 1px;
        background-color: transparent;
        position: relative;
        left: -6px;
        top: -6px;
        display: block;
        content: '';
    }

    .color-picker .color-picker-circle.color-picker-circle-black::before {
        border-color: #000000;
    }

    .color-picker .color-picker-circle.color-picker-circle-white::before {
        border-color: #ffffff;
    }
</style>