<template>
    <div class="condition">
        <span>{{ blockObject.title }}</span>
        <span>{{ blockObject.beforeInput }}</span>
        <label>
            <input 
                v-if="blockObject.inputType != 'date'"
                :type="blockObject.inputType" 
                :placeholder="blockObject.placeholder1" 
                :value="blockObject.value1" 
                @input="input1($event)" 
                v-model="blockObject.value1"
            />
            <DatePicker v-else :startValue='blockObject.value1' @input="input1($event)"></DatePicker>
            <span v-if="blockObject.inputType == 'checkbox'" class="styled-check"></span>
            <div class="options" v-if="blockObject.variables" v-show="currentVariables.length > 0 && showVariables">
                <ul>
                    <li class="opt" v-for="(opt, i) in currentVariables" :key="i" @click="selectOption(opt)">{{ opt }}</li>
                </ul>
            </div>
        </label>
        <span v-if="blockObject.inputs == 'double'">{{ blockObject.betweenInput }}</span>
        <input 
            :type="blockObject.inputType" 
            :placeholder="blockObject.placeholder2" 
            v-if="blockObject.inputs == 'double' && blockObject.inputType != 'date'" 
            :value="blockObject.value2" 
            @input="input2($event)" 
            v-model="blockObject.value2"
        />
        <DatePicker v-if="blockObject.inputs == 'double' && blockObject.inputType == 'date'"  :startValue='blockObject.value2' @input="input2($event)"></DatePicker>
        <span>{{ blockObject.afterInput }}</span>
    </div>
</template>

<script>

import DatePicker from './date-picker.vue'

export default {
    name: 'Condition',
    data() {
        return {
            showVariables: false,
            currentVariables: [],
        }
    },
    props: {
        blockObject: Object
    },
    methods: {
        selectOption(value){
            this.blockObject.value1 = value;
            this.showVariables = false;
        },
        input1($event){
            if (!isNaN(+$event)) {
                this.blockObject.value1 = $event;
            } else {
                let value = this.blockObject.value1.toLowerCase();
                if (value != '' && this.blockObject.variables && this.blockObject.variables !== 'false') {
                    this.showVariables = true;
                    this.currentVariables = this.blockObject.variables.filter((el) => {
                        if (el.toLowerCase().indexOf(value) >= 0) {
                            return el;
                        }
                    })
                } else {
                    this.currentVariables = this.blockObject.variables;
                }
            }
               
            this.$emit('child_input', {
                event: this.blockObject,
                number: 1
            });
        },
        input2($event){
            console.log(!isNaN(+$event));
            if (!isNaN(+$event)) {
                this.blockObject.value2 = $event;
            }
            this.$emit('child_input', {
                event: this.blockObject,
                number: 2
            });
        },
    },
    created() {
        document.addEventListener('click', (e) => {
            this.showVariables = false;
        });
    },
    components: {
        DatePicker
    }
}

</script>

<style lang="sass">
    .condition
        display: flex
        align-items: center
        padding: 7px 15px
        margin-bottom: 15px
        border: 1px solid #eee
        border-radius: 5px
        input, span
            display: block
            margin: 0 5px
            max-width: 200px
        input:not(.mx-input)
            border: 0
            border-bottom: 1px solid #eee
            width: max-content
            padding: 3px 10px
            &:focus
                outline: none
        .mx-icon-calendar, .mx-icon-clear
            right: 12px
        label
            position: relative
            input[type="checkbox"]
                display: none
            span
                position: relative
                &:before
                    content: ""
                    display: inline-block
                    width: 16px
                    height: 16px
                    margin-right: 10px
                    position: absolute
                    left: 0
                    bottom: 1px
                    background-color: #f0f0f0
                    box-shadow: inset 0px 2px 3px 0px rgba(0, 0, 0, .3), 0px 1px 0px 0px rgba(255, 255, 255, .8)
                    border-radius: 2px
            input[type="checkbox"]:checked + span:before 
                content: "\2713"
                text-shadow: 1px 1px 1px rgba(0, 0, 0, .2)
                font-size: 15px
                color: #999
                font-weight: 900
                text-align: center
                line-height: 15px
            .options
                position: absolute
                top: 30px
                left: 0
                width: 100%
                max-height: 200px
                border: 1px solid #eee
                background-color: #fff
                border-radius: 5px
                overflow: auto
                z-index: 2
                .opt
                    padding: 5px 10px
                    font-size: 12px
                    &:hover
                        cursor: pointer
                        background-color: #eee

</style>