<template>
    <date-picker v-model="value" type="date" @input="selectDate($event)" value-type="timestamp"></date-picker>
</template>

<script>
    import DatePicker from 'vue2-datepicker';
    import 'vue2-datepicker/locale/ru';
    import 'vue2-datepicker/index.css';

    export default {
        components: { DatePicker },
        props: {
            startValue: String | Number
        },
        data() {
            return {
                value: this.startValue ? new Date(Number(this.startValue)).getTime() : ''
            };
        },
        methods: {
            selectDate($event) {
                this.$emit('input', $event);
                // console.log($event);
            }
        },
        created() {
            this.selectDate(this.value);
        }
    };
</script>